body {
  background-color: #282c34;
}

.App {
  text-align: center;
  margin-top: 10px;
  margin-left: 20px;
  margin-right: 20px;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: left;
  justify-content: space-between;
  padding: 40px 20px;
}

h1 {
  font-family: 'Syncopate', sans-serif;
  font-weight: 700;
  font-size: 36px;
  color: #fff;
  text-shadow: 3px 3px #C90B73;
  text-transform: uppercase;
  margin: 20px 0px;
}

h3 {
  font-family: 'Syncopate', sans-serif;
  font-weight: 400;
  font-size: 18px;
  color: #fff;
  text-transform: uppercase;
  margin: 5px 0px;
}

p {
  font-weight: 200;
  font-size: 18px;
  color: #fff;
}